import Link from "next/link";

export function GeoBanner({
  state,
  locale = "en",
}: {
  state?: { name: string; abbr: string } | null;
  locale?: "en" | "es";
}) {
  if (!state) {
    return null;
  }

  return (
    <div className="bg-blue-100/80 text-blue-900 shadow-inner">
      <div className="mx-auto flex max-w-6xl flex-col items-start gap-2 px-4 py-3 text-sm lg:flex-row lg:items-center lg:justify-between">
        <div>
          <span className="font-semibold">
            {locale === "es" ? "Creemos que estás en" : "We think you're in"} {state.name}.
          </span>
          <span className="ml-2 text-blue-700">
            {locale === "es"
              ? "Revisa los límites y requisitos estatales actualizados."
              : "Review the latest state limits and requirements."}
          </span>
        </div>
        <Link
          href={`${locale === "es" ? "/es" : ""}/states/${state.abbr.toLowerCase()}`}
          className="rounded-full bg-white px-4 py-2 font-semibold text-blue-600 shadow-sm hover:bg-blue-50"
        >
          {locale === "es" ? "Ver reglas" : "See state rules"}
        </Link>
      </div>
    </div>
  );
}
