import type { ReactNode } from "react";
import { headers } from "next/headers";
import { SiteHeader } from "@/components/layout/site-header";
import { SiteFooter } from "@/components/layout/site-footer";
import { ChatPanel } from "@/components/ai/chat-panel";
import { GeoBanner } from "@/components/geo/geo-banner";
import { getStateByAbbr } from "@/data/states";

export default async function SiteLayout({
  children,
}: {
  children: ReactNode;
}) {
  const headerList = await headers();
  const detectedStateAbbr = headerList.get("x-geo-state") ?? undefined;
  const detectedState = detectedStateAbbr ? getStateByAbbr(detectedStateAbbr) : null;

  return (
    <div className="layout-grid">
      <SiteHeader locale="en" />
      <GeoBanner
        locale="en"
        state={
          detectedState
            ? { name: detectedState.name, abbr: detectedState.abbr }
            : null
        }
      />
      <main className="flex-1">
        {children}
      </main>
      <SiteFooter locale="en" />
      <ChatPanel locale="en" />
    </div>
  );
}
