import type { SupportedLocale } from "./translations";

type NavLink = {
  label: string;
  href: string;
};

type Navigation = {
  primary: NavLink[];
  legal: NavLink[];
};

const enNav: Navigation = {
  primary: [
    { label: "States", href: "/states" },
    { label: "Cities", href: "/cities" },
    { label: "Compare", href: "/compare" },
    { label: "Learn", href: "/learn" },
    { label: "Lenders", href: "/lenders" },
    { label: "Contact", href: "/contact" },
  ],
  legal: [
    { label: "Privacy", href: "/privacy" },
    { label: "Terms", href: "/terms" },
    { label: "CCPA", href: "/ccpa" },
    { label: "Disclosures", href: "/disclosures" },
  ],
};

const esNav: Navigation = {
  primary: [
    { label: "Estados", href: "/es/states" },
    { label: "Ciudades", href: "/es/cities" },
    { label: "Comparar", href: "/es/compare" },
    { label: "Aprender", href: "/es/learn" },
    { label: "Prestamistas", href: "/es/lenders" },
    { label: "Contacto", href: "/es/contact" },
  ],
  legal: [
    { label: "Privacidad", href: "/es/privacy" },
    { label: "Términos", href: "/es/terms" },
    { label: "CCPA", href: "/es/ccpa" },
    { label: "Avisos", href: "/es/disclosures" },
  ],
};

export function getNavigation(locale: SupportedLocale): Navigation {
  return locale === "es" ? esNav : enNav;
}
